.isDark {
    background-color: black;
}
.titlebar {
    text-align: center;
}
.titlebar > h1 {
    font-family: 'Luckiest Guy', cursive;
    font-size: 10rem;
    color: yellow;
    letter-spacing: .01rem;
    text-shadow: 6px 6px 0px rgba(0,0,0,0.2);
}

.titlebar > h1:hover {
    font-size: 13rem;
}
#titlebar-jumbotron {
    max-height: 300px;
    background-image: url(/turbo-potato-react-game/static/media/eggBkg.db88f0e1.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; 
}


@media only screen and (max-width: 960px) {
    .titlebar > h1 {
        font-size: 6em;
    }
    .titlebar > h1:hover {
        font-size: 6.5em;
    }
    
}


@media only screen and (max-width: 680px) {
    .titlebar > h1 {
        font-size: 5em;
    }
    .titlebar > h1:hover {
        font-size: 5.5em;
    }
    
}

.scoreBox {
    border: 2pt solid green;
}

#scoreboard-jumbotron {

    background-color: pink;
}

#col {
    border: red solid 5pt;
}
.goalArea {
    border: 5pt solid black;
}
#playboxImg {
    height: 200pt;
    width: 160pt;
}

.playbox:hover {
    -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

@media only screen and (max-width: 680px) {
    #playboxImg {
        height: 100pt;
        width: 80pt;
    }
    
    .playbox:hover {
        -webkit-transform: scaleX(-1);
      transform: scaleX(-1);
    }
  }
.playArea {
    display: flex;
    justify-content: space-around;

}

@media only screen and (max-width: 680px) {
    .playArea {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
    
    }
}
